
#app .vux-header {
  background: #fff;
}
a {
  color: #000;
}
.foot {
  height: 20vh;
  width: 100%;
}
.foot p {
  width: 40%;
  height: 25%;
  margin: 10% auto;
}
.foot .span1 {
  width: 30%;
  height: 30%;
  background: orange;
  color: #fff;
  text-align: center;
  border-radius: 5px;
  line-height: 40px;
}
.fa {
  display: block!important;
  margin-left: 6px;
}
.flex-demo {
  text-align: center;
  color: #000;
  border-radius: 4px;
  margin: 10px 0;
  background-clip: padding-box;
}
#app .vux-header .vux-header-title {
  margin: 0 10px;
}
.vux-header .vux-header-title .span1 {
  float: left;
}
.vux-header .vux-header-title .span2 {
  float: right;
}
